import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface IProduct {
    type: string;
    id: number;
    name: string;
    gost: string;
    price: number;
    img?: string;
}

interface IProductType {
    id: number;
    type: string;
}

interface ProductState {
    types: IProductType[];
    gosts: string[];
    products: IProduct[];
}

